import {Numeric} from "../numeric";

export class Fraction {
    private _numerator: number;
    private _denominator: number;

    constructor(value?: unknown, denominatorOrPeriodic?: number) {
        this._numerator = 1;
        this._denominator = 1;

        return this.parse(value, denominatorOrPeriodic);
    }

    get isFraction() {
        return true;
    }

    // ------------------------------------------
    // Getter and setter
    // ------------------------------------------

    get numerator(): number {
        return this._numerator;
    }

    set numerator(value: number) {
        this._numerator = value;
    }

    get denominator(): number {
        return this._denominator;
    }

    set denominator(value: number) {
        this._denominator = value;
    }

    get value(): number {
        return this._numerator / this._denominator;
    }

    // Display getter
    get tex(): string {
        if (this._denominator === 1) {
            return `${this._numerator}`;
        } else if (this._numerator < 0) {
            return `-\\frac{ ${-this._numerator} }{ ${this._denominator} }`;
        } else {
            return `\\frac{ ${this._numerator} }{ ${this._denominator} }`;
        }
    }

    get display(): string {
        if (this._denominator === 1) {
            return `${this._numerator}`;
        } else {
            return `${this._numerator}/${this._denominator}`;
        }
    }

    // Helper function to display fractions
    get frac(): string {
        return this.tex;
    }

    get dfrac(): string {
        return this.tex.replace('\\frac', '\\dfrac');
    }

    // ------------------------------------------
    // Creation / parsing functions
    // ------------------------------------------
    /**
     * Parse the value to get the numerator and denominator
     * @param value : number or string to parse to get the fraction
     * @param denominatorOrPeriodic (optional|number) : length of the periodic part: 2.333333 => 1 or denominator value
     */
    parse = (value: unknown, denominatorOrPeriodic?: number): Fraction => {
        let S: string[];

        // A null value means a zero fraction.
        if (value === null) {
            this._numerator = 0;
            this._denominator = 1;
            return this;
        }

        switch (typeof value) {
            case "string":
                // Split the sting value in two parts: Numerator/Denominator
                S = value.split('/');

                // Security checks
                if (S.length > 2) throw "Two many divide signs";
                if (S.map(x => x === '' || isNaN(Number(x))).includes(true)) throw "Not a number"

                if (S.length === 1) {
                    // No divide sign
                    return this.parse(+S[0]);
                } else if (S.length === 2) {
                    // One divide signe
                    // We check if the denominator is zero
                    if (S[1] === '0') {
                        this._numerator = NaN;
                        this._denominator = 1;
                    } else {
                        this._numerator = +S[0];
                        this._denominator = +S[1];
                    }
                } else {
                    // More than one divide sign ?
                    this._numerator = NaN;
                    this._denominator = 1;
                }
                break;
            case "number":
                if (Number.isSafeInteger(value)) {
                    // The given value is an integer
                    this._numerator = +value;

                    if (denominatorOrPeriodic === undefined || !Number.isSafeInteger(denominatorOrPeriodic)) {
                        this._denominator = 1;
                    } else {
                        this._denominator = +denominatorOrPeriodic;
                    }
                } else {
                    // The given value is a float number

                    // Get the number of decimals after the float sign
                    let p: number = (value.toString()).split('.')[1].length;

                    // Transform the float number in two integer
                    if (denominatorOrPeriodic === undefined) {
                        this._numerator = value * Math.pow(10, p);
                        this._denominator = Math.pow(10, p);
                    } else if (Number.isSafeInteger(denominatorOrPeriodic)) {
                        this._numerator = value * Math.pow(10, p) - Math.floor(value * Math.pow(10, p - denominatorOrPeriodic));
                        this.denominator = Math.pow(10, p) - Math.pow(10, p - denominatorOrPeriodic)
                    }
                }
                break;
            case "object":
                if (value instanceof Fraction) {
                    this._numerator = +value.numerator;
                    this._denominator = +value.denominator;
                }
                break;
        }
        return this;
    };

    /**
     * Clone the current fraction
     */
    clone = (): Fraction => {
        let F = new Fraction();
        F.numerator = +this._numerator;
        F.denominator = +this._denominator;
        return F;
    };

    /**
     * Define the fraction as zero
     */
    zero = (): Fraction => {
        this._numerator = 0;
        this._denominator = 1;
        return this;
    };

    /**
     * Define the fraction as neutral (one)
     */
    one = (): Fraction => {
        this._numerator = 1;
        this._denominator = 1;
        return this;
    };

    /**
     * Define the fraction as (positive) infinite
     */
    infinite = (): Fraction => {
        this._numerator = Infinity;
        this._denominator = 1;
        return this;
    };

    /**
     * Define the fraction as not a number (means an error)
     */
    invalid = (): Fraction => {
        this._numerator = NaN;
        this._denominator = 1;
        return this;
    };

    // ------------------------------------------
    // Mathematical operations
    // ------------------------------------------
    /**
     * Set the fraction to it's opposite
     */
    opposed = (): Fraction => {
        this._numerator = -this._numerator;
        return this;
    };

    /**
     * Add another fraction
     * @param F Fraction
     */
    add = (F: unknown): Fraction => {
        let Q = new Fraction(F)

        let N: number = this._numerator,
            D: number = this._denominator;

        this._numerator = N * Q.denominator + Q.numerator * D;
        this._denominator = D * Q.denominator;

        return this.reduce();
    };

    /**
     * Subtract another fraction
     * @param F Fraction
     */
    subtract = (F: unknown): Fraction => {
        return this.add(new Fraction(F).opposed());
    };

    /**
     * Multiply by another Fraction or number
     * @param F
     */
    multiply = (F: unknown): Fraction => {
        // Parse the value.
        // If it's a fraction, return a clone of it
        // If it's an integer, return the fraction F/1
        let Q = new Fraction(F);

        this._numerator = this._numerator * Q.numerator;
        this._denominator = this._denominator * Q.denominator;

        return this.reduce();
    };

    /**
     * Divide by a fraction
     * @param F Fraction
     */
    divide = (F: unknown): Fraction => {
        let Q = new Fraction(F);

        // The divider is null
        if (Q.isZero()) {
            return new Fraction().infinite()
        }

        // Dividing = multiply by invert
        return this.multiply(Q.invert())
    };

    /**
     * Invert the fraction
     */
    invert = (): Fraction => {
        if (this.isZero()) {
            return this.infinite()
        }

        let n = +this._numerator, d = +this._denominator;
        this._numerator = d;
        this._denominator = n;

        return this;
    }

    /**
     * Pow the fraction...
     * @param p integer
     */
    pow = (p: number): Fraction => {
        // The power number must be an integer.
        if (!Number.isSafeInteger(p)) {
            return this.invalid();
        }

        // Trivial pow
        if (p === 0) {
            return this.one()
        }
        if (p === 1) {
            return this
        }
        if (p === -1) {
            return this.invert()
        }

        // Reduce the fraction before pow
        this.reduce();

        // The power is negative -> invert the fraction.
        if (p < 0) {
            this.invert()
        }

        // Pow each number
        this._numerator = this._numerator ** Math.abs(p);
        this._denominator = this._denominator ** Math.abs(p);
        return this;
    };

    /**
     * Get the root value of a fraction
     * @param p integer
     */
    root = (p: number): Fraction => {
        // TODO: nth - root of a fraction => this will return another type of coefficient.

        // Trivial roots
        if (p === 0) {
            return this.invalid();
        }
        if (p === 1) {
            return this;
        }
        if (p === -1) {
            return this.invert();
        }

        /* If negative, invert the fraction*/
        if (p < 0) {
            this.invert()
        }

        // TODO: root: Actually, it gives floating number
        this._numerator = Math.pow(this._numerator, Math.abs(1 / p));
        this._denominator = Math.pow(this._denominator, Math.abs(1 / p));
        return this;
    }

    /**
     * Return the square root of the fraction
     */
    sqrt = (): Fraction => {
        return this.root(2);
    }

    /**
     * Return the absolute value of the fraction
     */
    abs = (): Fraction => {
        this._numerator = Math.abs(this._numerator);
        this._denominator = Math.abs(this._denominator);
        return this;
    };

    // ------------------------------------------
    // Mathematical operations specific to fractions
    // ------------------------------------------
    /**
     * Reduce the fraction
     */
    reduce = (): Fraction => {
        let g = Numeric.gcd(this._numerator, this._denominator);
        this._numerator = this._numerator / g;
        this._denominator = this._denominator / g;

        if (this._denominator < 0) {
            this._denominator = -this._denominator;
            this._numerator = -this._numerator;
        }
        return this;
    };

    /**
     * Amplify the fraction by a value
     * @param k integer
     */
    amplify = (k: number): Fraction => {
        if (Number.isSafeInteger(k)) {
            this._numerator *= k;
            this._denominator *= k;
        }
        return this;
    };


    // ------------------------------------------
    // Compare functions
    // ------------------------------------------
    /**
     * Compare the current coefficient with another coefficient
     * @param F (Coefficient) The coefficient to compare
     * @param sign (string| default is =): authorized values: =, <, <=, >, >= with some variations.
     */
    compare = (F: Fraction, sign?: string): boolean => {
        if (sign === undefined) {
            sign = '=';
        }


        switch (sign) {
            case '>':
                return this.value > F.value;
            case ">=" || "=>" || "geq":
                return this.value >= F.value;
            case "<":
                return this.value < F.value;
            case "<=" || "=>" || "leq":
                return this.value <= F.value;
            case "=":
                // let F2: Fraction = F.clone().reduce(),
                //     F1: Fraction = this.clone().reduce();
                // return (F1.numerator === F2.numerator && F1.denominator === F2.denominator);
                return this.value === F.value;
            case "<>":
                return this.value !== F.value;
            default:
                return false;
        }
    };
    /* Compare shortcuts */

    /**
     * Compare less than fraction
     * @param than
     */
    lesser = (than: Fraction): Boolean => {
        return this.compare(than, '<');
    };
    /**
     * Compare less or equal than fraction
     * @param than
     */
    leq = (than: Fraction): Boolean => {
        return this.compare(than, '<=');
    };
    /**
     * Compare greater than fraction
     * @param than
     */
    greater = (than: Fraction): Boolean => {
        return this.compare(than, '>');
    };
    /**
     * Compare greater or equal than fraction
     * @param than
     */
    geq = (than: Fraction): Boolean => {
        return this.compare(than, '>=');
    };
    /**
     * Compare is equal to fraction
     * @param than
     */
    isEqual = (than: Fraction): boolean => {
        return this.compare(than, '=');
    }
    /**
     * Compare: is different to fraction
     * @param than
     */
    isDifferent = (than: Fraction): boolean => {
        return this.compare(than, '<>');
    }
    /**
     * Compare: is opposed to fraction
     * @param p
     */
    isOpposed = (p: Fraction): boolean => {
        return this.isEqual(p.clone().opposed());
    }
    /**
     * Compare: is invert to fraction
     * @param p
     */
    isInverted = (p: Fraction): boolean => {
        return this.isEqual(new Fraction().one().divide(p.clone()));
    }
    /**
     * Compare: is equal to zero
     */
    isZero = (): boolean => {
        return this._numerator === 0;
    }
    /**
     * Compare: is equal to one
     */
    isOne = (): boolean => {
        return this._numerator === 1 && this._denominator === 1;
    }
    /**
     * Compare: is positive
     */
    isPositive = (): boolean => {
        return this.sign() === 1;
    }
    /**
     * Compare: is negative
     */
    isNegative = (): boolean => {
        return this.sign() === -1;
    }
    /**
     * Compare: is not a number (NaN)
     */
    isNaN = (): boolean => {
        return isNaN(this._numerator);
    }
    /**
     * Compare is inifinite
     */
    isInfinity = (): boolean => {
        return this._numerator === Infinity;
    }
    /**
     * Compare is finite ?
     */
    isFinite = (): boolean => {
        return !this.isInfinity();
    }
    /**
     * Compare: are the numerator and denominator perfect square ?
     */
    isSquare = (): boolean => {
        return Math.sqrt(this._numerator) % 1 === 0 && Math.sqrt(this._denominator) % 1 === 0
    }
    /**
     * Get the sign of the fraction
     */
    sign = (): number => {
        return (this._numerator * this._denominator >= 0) ? 1 : -1;
    };
}